<template>
  <div id="legal-pay-detail" :class="skins=='days'?'whites':'whites balck'">
    <div class="title radius4">
      <span v-if="msg.is_sure == 0">{{$t('td.waitpay')}}</span>
      <span v-if="msg.is_sure == 1">{{$t('td.finished')}}</span>
      <span v-if="msg.is_sure == 2">{{$t('td.ceiled')}}</span>
      <span v-if="msg.is_sure == 3">{{$t('td.payed')}}</span>
      <div v-if="msg.is_sure == 0">{{$t('fat.pwaitPay')}}</div>
      <div v-if="msg.is_sure == 1">{{$t('fat.odFinish')}}</div>
      <div v-if="msg.is_sure == 2">{{$t('fat.odCeil')}}</div>
      <div v-if="msg.is_sure == 3">{{$t('fat.odPay')}}</div>
    </div>
    <div class="info ft14 radius4">
      <div>
        <span>{{$t('td.tradeTotal')}}：</span>
        <span>{{msg.deal_money || '0.000' | numFilters(3)}}</span>
      </div>
      <div>
        <span>{{msg.is_seller=='0'?$t('td.seller'):$t('td.buyer')}}：</span>
        <span>{{msg.seller_name}}</span>
      </div>
     
      <div>
        <span>{{$t('td.price')}}：</span>
        <span>{{msg.price || '0.000' | numFilters(3)}}</span>
      </div>
      <div>
        <span>{{$t('td.num')}}：</span>
        <span>{{msg.number || '0.000' | numFilters(3)}}{{msg.currency_name}}</span>
      </div>
      
      <div>
        <span>{{$t('td.callWay')}}：</span>
        <span>{{msg.seller_phone}}</span>
      </div>
      <div>
        <span>{{$t('td.placeTime')}}：</span>
        <span>{{msg.format_create_time}}</span>
      </div>
      
      <div>
        <span>{{$t('td.renum')}}：</span>
        <span>{{msg.id}}</span>
      </div>
      <div class="btns">
       
        <div class="btn" @click="hasPay = true" v-if="msg.is_sure == 3&&msg.type=='buy'">{{$t('fat.receivePays')}}</div>
      </div>
    </div>
    <!-- 取消交易 -->
     <el-dialog title :visible.sync="showCancel" width="300px" center>
      <div class="transfer-content">
        <h3>{{$t('fat.tdCeil')}}</h3>
        <div class="transfer-list">
          <p>{{$t('fat.pCeil')}}</p>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="cancel">{{$t('cuy.confirm')}}</el-button>
      </span>
    </el-dialog>
   
    <!-- 付款确认 -->
     <el-dialog title :visible.sync="showConfirm" width="300px" center>
      <div class="transfer-content">
        <h3>{{$t('fat.paySure')}}</h3>
        <div class="transfer-list">
          <p>{{$t('fat.pdopay')}}</p>
          <p>{{$t('fat.badClick')}}</p>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="confirm">{{$t('cuy.confirm')}}</el-button>
      </span>
    </el-dialog>
    
    <el-dialog title :visible.sync="hasPay" width="300px" center>
      <div class="transfer-content">
        <h3>{{$t('fat.receivePay')}}</h3>
        <div class="transfer-list">
          <p>{{$t('fat.payReceive')}}</p>
          <p>{{$t('fat.badClick')}}</p>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="hadPay">{{$t('cuy.confirm')}}</el-button>
      </span>
    </el-dialog>
    
  </div>
</template>

<script>
export default {
  data() {
    return {
      msg: "",
      token:'',
      showConfirm:false,
      showCancel:false,
      hasPay:false,
      id:'',
      msg:{},
      skins: localStorage.getItem("skin") || "days"
    };
  },
  created() {
    var token = window.localStorage.getItem("token") || "";
      // this.id = this.$route.query.id;
    if (token) {
      this.token = token;
      this.id = this.$route.query.id || '';
      this.getMsg()
    }
  },
  methods: {
    getMsg(){
      var i = layer.load();
      this.$http({
        url:'/api/legal_deal',
        params:{id:this.id},
        headers:{Authorization:this.token}
      }).then(res => {
        layer.close(i);
        if(res.data.type == 'ok'){
          this.msg = res.data.message;
        }
      })
    },
    getData() {
      var i = layer.load();
      this.$http({
        url: "/api/legal_deal",
        params: {
          id: this.id
        },
        headers: { Authorization: this.token }
      }).then(res => {
        layer.close(i);
        if (res.data.type == "ok") {
          this.msg = res.data.message;
        }
      });
    },
    // 取消交易
    cancel(){
      this.$http({
        url:'/api/user_legal_pay_cancel',
        method:'post',
        data:{id:this.id},
        headers:{Authorization:this.token}
      }).then(res => {
        lay.msg(res.data.message);
        setTimeout(function(){
          location.reload();
        },500)
      }).then(() => {
        this.showCancel = false;
      })
    },
    // 确认付款
    confirm(){
      this.$http({
        url:'/api/user_legal_pay',
        method:'post',
        data:{id:this.id},
        headers:{Authorization:this.token}
      }).then(res => {
        lay.msg(res.data.message);
        setTimeout(function(){
          location.reload();
        },500)
      }).then(() => {
        this.showConfirm = false;
      })
    },
    // 确认对方已付款
    hadPay(){
      this.$http({
        url:'/api/legal_deal_user_sure',
        method:'post',
        data:{id:this.id},
        headers:{Authorization:this.token}
      }).then(res => {
        layer.msg(res.data.message);
        this.hasPay = false;
        setTimeout(function(){
          location.reload();
        },500)
      })
    },
    evimgs(src){
      var that = this;
      return layer.open({
        type: 1 //Page层类型
        ,area: ['375px', '500px']
        ,title: ''
        ,shade: 0.6 //遮罩透明度
        ,anim: 1 //0-6的动画形式，-1不开启
        ,content: "<img src='"+src+"' alt='' class='openimg'>"
        ,btn: [that.$t('fat.closeBtn')],
         yes: function(index){
            layer.close(index);
        }

      });   
    }
  }
};
</script>

<style lang='scss' scoped>
.whites {
  width: 1200px;
  margin: 66px auto 50px;
  background-color: #fff;
  padding-bottom:30px;
  -webkit-box-shadow: 0 0 29px 0 rgba(32, 36, 48, 0.08);
  box-shadow: 0 0 29px 0 rgba(32, 36, 48, 0.08);
  min-height: 630px;
  position: relative;
  top: 18px;
  > .title {
    // background: #f8f8f8;
    margin-bottom: 20px;
    padding: 0  0 20px 30px;
    span{
      font-size: 20px;
      line-height: 50px;
      font-weight: bold;
    }
    div{
      font-size: 12px;
    }
  }
  > .info {
    // background: #f8f8f8;
    padding: 0 30px;
    line-height: 40px;
    >div{
      display: flex;
    }
    span:first-child{
      width:140px;
    }
    >.btns{
      padding: 20px 0;
      cursor: pointer;
      >div{
        color: #fff;
        border-radius: 2px;
        padding: 0 16px;
        background: #f0b90b;
        margin-right: 30px;
        font-size: 14px;
      }
      >.cancelBtn{
        background: #ccc;
        color: #333;
      }
    }
  }
  >.cancel-box,>.confirm-box{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.7);
    >.content{
      background: #fff;
      margin: 200px auto 0;
      border-radius: 2px;
      width: 360px;
      border-radius: 5px;
      line-height: 40px;
      text-align: center;
      >div:first-child{
        font-weight: 600;
      }
      >.flex{
        margin-top: 10px;
        border-top: 1px solid #cad7e0;
        cursor: pointer;
        div{
          width: 50%;
        }
        >div:first-child{
          border-right: 1px solid #cad7e0;
        }
      }
    }
  }
}
.openimg{display: block;margin: 0 auto;max-width: 100%;}
.transfer-content h3{
  text-align: center;
    color: #f0b90b;
    font-size: 18px;
}
.transfer-list{
  margin-top: 20px;
}
.transfer-list p{
  line-height: 30px;
  font-size: 14px;
}
.dialog-footer button{
  width: 150px;
  background-color: #f0b90b!important;
  border: 1px solid #f0b90b!important;
}
.balck{
  background-color: #1e2b34;
  color: #fff;
}
</style>



